import React, { useState } from 'react';

export default function ChangePasswordPage() {
    const [showOld, setShowOld] = useState(false);
    const [showNew, setShowNew] = useState(false);
    const [showRepeat, setShowRepeat] = useState(false);

    return (
        <div style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            minHeight: '100vh',
            background: '#f7f7f7'
        }}>
            <div style={{
                background: '#fff',
                borderRadius: 10,
                boxShadow: '0 2px 8px rgba(0,0,0,0.07)',
                padding: 32,
                width: 400,
                maxWidth: '95vw'
            }}>
                {/* Header */}
                <div style={{
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'space-between',
                    marginBottom: 24
                }}>
                    <button style={{ background: 'none', border: 'none', fontSize: 20, cursor: 'pointer' }}>{'‚Üê'}</button>
                    <h2 style={{ margin: 0, fontWeight: 600 }}>Change password</h2>
                    <button style={{ background: 'none', border: 'none', fontSize: 20, cursor: 'pointer' }}>{'‚úï'}</button>
                </div>

                {/* Old Password */}
                <div style={{ marginBottom: 20 }}>
                    <label style={{ fontSize: 14, color: '#222' }}>old password</label>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        border: '1px solid #27ae60',
                        borderRadius: 6,
                        marginTop: 6,
                        paddingRight: 8
                    }}>
                        <input
                            type={showOld ? 'text' : 'password'}
                            placeholder="Enter your password"
                            style={{
                                border: 'none',
                                outline: 'none',
                                flex: 1,
                                padding: '10px 12px',
                                fontSize: 15,
                                borderRadius: 6
                            }}
                        />
                        <button
                            type="button"
                            onClick={() => setShowOld(s => !s)}
                            style={{ background: 'none', border: 'none', cursor: 'pointer' }}
                            aria-label="Toggle password visibility"
                        >üëÅÔ∏è</button>
                    </div>
                    <div style={{ textAlign: 'right', marginTop: 4 }}>
                        <a href="#" style={{ color: '#27ae60', fontSize: 13, textDecoration: 'none' }}>Forget password ?</a>
                    </div>
                </div>

                {/* New Password */}
                <div style={{ marginBottom: 20 }}>
                    <label style={{ fontSize: 14, color: '#222' }}>New Password</label>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        border: '1px solid #27ae60',
                        borderRadius: 6,
                        marginTop: 6,
                        paddingRight: 8
                    }}>
                        <input
                            type={showNew ? 'text' : 'password'}
                            placeholder="Enter your password"
                            style={{
                                border: 'none',
                                outline: 'none',
                                flex: 1,
                                padding: '10px 12px',
                                fontSize: 15,
                                borderRadius: 6
                            }}
                        />
                        <button
                            type="button"
                            onClick={() => setShowNew(s => !s)}
                            style={{ background: 'none', border: 'none', cursor: 'pointer' }}
                            aria-label="Toggle password visibility"
                        >üëÅÔ∏è</button>
                    </div>
                </div>

                {/* Repeat New Password */}
                <div style={{ marginBottom: 28 }}>
                    <label style={{ fontSize: 14, color: '#222' }}>Repeat new password</label>
                    <div style={{
                        display: 'flex',
                        alignItems: 'center',
                        border: '1px solid #27ae60',
                        borderRadius: 6,
                        marginTop: 6,
                        paddingRight: 8
                    }}>
                        <input
                            type={showRepeat ? 'text' : 'password'}
                            placeholder="Enter your New Password"
                            style={{
                                border: 'none',
                                outline: 'none',
                                flex: 1,
                                padding: '10px 12px',
                                fontSize: 15,
                                borderRadius: 6
                            }}
                        />
                        <button
                            type="button"
                            onClick={() => setShowRepeat(s => !s)}
                            style={{ background: 'none', border: 'none', cursor: 'pointer' }}
                            aria-label="Toggle password visibility"
                        >üëÅÔ∏è</button>
                    </div>
                </div>

                {/* Save Button */}
                <button
                    style={{
                        width: '100%',
                        background: '#27ae60',
                        color: '#fff',
                        border: 'none',
                        borderRadius: 6,
                        padding: '12px 0',
                        fontSize: 17,
                        fontWeight: 500,
                        cursor: 'pointer',
                        transition: 'background 0.3s'
                    }}
                    onClick={() => { }}
                    aria-label="Save password"
                >
                    Save password
                </button>
            </div>
        </div>
    );
}
